.loader {
  border: 16px solid #f3f3f3; 
  border-top: 16px solid rgb(51, 135, 219); 
  border-bottom: 16px solid rgb(51, 135, 219); 
  border-radius: 50%;
  width: 1.5rem;  
  height: 1.5rem;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}